import React from "react";
import style from "../login.module.scss";
import {ErrorMessage, Field, Form, Formik} from "formik";

export const LoginForm = ({onSubmit, image}) => {
    return (
        <Formik initialValues={{email: "", password: "", captcha: ""}} onSubmit={onSubmit}>
            <Form className={style.form}>
                <div>
                    <Field component={"input"} name={"email"} className={style.input}/>
                    <ErrorMessage name={"email"}/>
                </div>
                <div>
                    <Field component={"input"} name={"password"} type={"password"} className={style.input}/>
                    <ErrorMessage name={"password"}/>
                </div>
                {image ? <img src={image} alt="" style={{width: "200px", height: "200px"}}/> : null}
                <Field name={"captcha"}/>
                <button type={"submit"}>Send</button>
            </Form>
        </Formik>
    );
};